import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { data } from '../portfolio-page/portfolio-data.mock';
import { PortfolioItem } from '../portfolio-page/portfolio-item.interface';

@Component({
  selector: 'app-portfolio-item',
  templateUrl: './portfolio-item.component.html',
  styleUrls: ['./portfolio-item.component.scss']
})
export class PortfolioItemComponent implements OnInit {
  data: PortfolioItem[] = data;
  data$: Observable<PortfolioItem>;

  constructor(
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.getData();
  }

  getData(): void {
    this.data$ = this.route.paramMap.pipe(
      map(params => params.get('slug')),
      map(slug => this.data.find(item => item.slug === slug))
    );
  }

}
